import { toolsConfigManager } from '../config';

/**
 * GM_XHR
 * @typedef GMResponse
 * @property {*} response response
 * @property {number} status
 */

/**
 * GMGM.xmlHttpRequest
 * @callback GMCallback
 * @param {Object} options 
 * @returns {Promise<GMResponse>} 
 */

/**
 * GM API
 * @param {GMCallback} gmCallback 
 * @param {string} url 
 * @returns {Promise<*>} 
 */
const getDataByGM = async (gmCallback, url) => {
  console.debug(`GM_XHR, URL:${url}`);
  const response = await gmCallback({
    method: 'get',
    url: url,
    responseType: 'blob',
  });
  console.debug(`${url}:`, response);
  if (response.status !== 200) {
    const errMsg = `GM_XHR ! : ${response.status}`;
    console.error(errMsg);
    throw new Error(errMsg);
  }
  return response.response;
};
/**
 * fetch
 * @param {string} url 
 * @returns {Promise<Blob>} 
 */
const getDataByFetch = async (url) => {
  console.debug(`fetch, URL:${url}`);
  const response = await fetch(url);
  console.debug(`${url}:`, response);
  if (!response.ok) {
    const errMsg = `fetch ! : ${response.status}`;
    console.error(errMsg);
    throw new Error(errMsg);
  }
  const blob = await response.blob();
  return blob;
};
/**
 * GMfetch
 * @param {string} url 
 * @param {GMCallback} callback GMGM.xmlHttpRequest
 * @returns {Promise<*>} 
 */
export const getData = async (url, callback = null) => {
  const downloadConfig = toolsConfigManager.config.download;
  let tryNum = downloadConfig.tryNum;
  console.debug(`${tryNum}`);
  let res = null;
  let currentE = null;
  while (tryNum >= 0) {
    try {
      if (callback === null) {
        res = await getDataByFetch(url);
      } else {
        res = await getDataByGM(callback, url);
      }
      return res;
    } catch (e) {
      tryNum--;
      currentE = e;
      console.info(`${tryNum}${url}`);
    }
  }
  throw currentE;
};
