import JSZip from 'jszip';
import { getData } from './download';
import { saveAs } from 'file-saver';
import { toolsConfigManager } from '../config';

/**
 * zip
 * @param {string} url 
 * @param {JSZip} zip zip
 * @param {import("./download").GMCallback} gmCallback fetch
 */
const getDataAndAdd = async (url, zip, gmCallback = null) => {
  console.log(`: ${url}`);
  const content = await getData(url, gmCallback);
  const name = url.split('/').pop();
  console.log(`: ${name}`);
  zip.file(name, content, { binary: true });
  console.log(`zip: ${name}`);
};

/**
 * @callback PCallback
 * @param {number} successNum
 * @param {number} allNum
 * @returns {void}
 */

/**
 * @typedef {Object} DAZOptions
 * @property {string} zipName - 
 * @property {import("./download").GMCallback} gmCallback - fetch
 * @property {string} extention - 
 * @property {PCallback} progressCallback - 
 */

/** @type {DAZOptions}*/
const DAZDefaultOptions = {
  zipName: 'archive',
  gmCallback: null,
  extention: 'zip',
  progressCallback: (success, all) => {
    console.info(`${success}/${all}`);
  },
};

/**
 * 
 * @param {string[]} linkList 
 * @param {DAZOptions} [options] 
 */
export const downloadAndZipSync = async (linkList, options = {}) => {
  const { zipName, gmCallback, extention, progressCallback } = {
    ...DAZDefaultOptions,
    ...options,
  };

  console.debug(':', linkList);
  console.debug(':', zipName);
  console.debug(':', gmCallback ? 'GMCallback' : 'fetch');
  console.debug(':', extention);

  if (linkList.length <= 0) {
    console.warn('');
    return;
  }

  console.info('');
  console.debug('zip');
  const zip = new JSZip();
  // 
  console.info('');

  let successNum = 0;
  let allNum = linkList.length;
  for (const url of linkList) {
    try {
      await getDataAndAdd(url, zip, gmCallback);
      successNum++;
    } catch (e) {
      console.warn(`: ${url}`, e);
    } finally {
      progressCallback(successNum, allNum);
    }
  }
  console.info(`, :${successNum}, :${allNum}`);
  console.info('');
  const content = await zip.generateAsync({ type: 'blob' });
  console.info('');
  saveAs(content, ''.concat(zipName, '.', extention));
  console.info(`: ${zipName}.${extention}`);
};

/**
 * 
 * @param {string[]} linkList 
 * @param {DAZOptions} [options] 
 */
export const downloadAndZipAsync = async (linkList, options = {}) => {
  const { zipName, gmCallback, extention, progressCallback } = {
    ...DAZDefaultOptions,
    ...options,
  };
  const maxDownload = toolsConfigManager.config.file.maxDownload;

  console.debug(':', linkList);
  console.debug(':', zipName);
  console.debug(':', gmCallback ? 'GMCallback' : 'fetch');
  console.debug(':', extention);
  console.debug(':', maxDownload);

  if (linkList.length <= 0) {
    console.warn('');
    return;
  }
  console.info('');
  console.debug('zip');
  const zip = new JSZip();

  const incrementSuccessNum = async () => {
    successNum++;
  };

  const allNum = linkList.length;
  let successNum = 0;
  const downloadQueue = [];
  for (const url of linkList) {
    const downloadTask = async () => {
      try {
        await getDataAndAdd(url, zip, gmCallback);
        await incrementSuccessNum();
      } catch (error) {
        console.error(`: ${url}`, error);
      } finally {
        progressCallback(successNum, allNum);
      }
    };

    downloadQueue.push(downloadTask);

    if (downloadQueue.length >= maxDownload) {
      console.debug(':', downloadQueue.length);
      await Promise.all(downloadQueue.map((task) => task()));
      downloadQueue.length = 0; // 
    }
  }

  // 
  if (downloadQueue.length > 0) {
    console.debug(':', downloadQueue.length);
    await Promise.all(downloadQueue.map((task) => task()));
  }

  console.info(`, :${successNum}, :${allNum}`);
  console.info('');
  const content = await zip.generateAsync({ type: 'blob' });
  console.info('');
  saveAs(content, ''.concat(zipName, '.', extention));
  console.info(`: ${zipName}.${extention}`);
};
